/* Resetting default styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }

  .poppins-custom {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-style: normal;
}

  /* Navbar Styling */
  .navbar {
    background-color: #222; /* Dark background for navbar */
    color: white; /* White text color */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Subtle shadow effect */
    position: sticky; /* Sticky navbar on top */
    top: 0; /* Stays on top */
    z-index: 1000; /* Ensures it stays above other content */
  }
  

  /* Navigation Links Styling */
  .nav-links {
    list-style: none; /* Removes default list styling */
    display: flex; /* Horizontal layout */
    gap: 20px; /* Space between each link */
  }
  
  /* Individual Link Styling */
  .nav-links li {
    display: inline;
  }
  
  /* Link Styling */
  .nav-links li a {
    color: white;
    text-decoration: none; /* Removes underline */
    font-size: 18px;
    transition: 0.3s ease-in-out; /* Smooth transition for hover effect */
    padding: 8px 12px; /* Padding around links */
    border-radius: 5px; /* Rounded corners */
  }
  
  /* Hover Effect for Links */
  .nav-links li a:hover {
    background-color: #f6ff00; /* Yellow background on hover */
    color: black; /* Text color changes to black */
    transition: all 0.3s ease-in-out; /* Smooth transition */
  }
  
  /* Responsive Design for Small Screens */
  @media (max-width: 768px) {
    .navbar {
      flex-direction: column; /* Stacks the navbar items vertically */
      padding: 10px; /* Reduced padding for mobile view */
    }
  
    .nav-links {
      flex-direction: column; /* Vertical layout for links */
      text-align: center; /* Center align links */
      gap: 10px; /* Spacing between links */
      margin-top: 10px;
    }
  
    .nav-links li {
      display: block; /* Makes links stack vertically */
    }
  }
  

/* Styling the Search_bar Componenet */
#search {
    width: 100%; /* Full width */
    max-width: 95%; /* Limit max width */
    margin: 22px;
    margin-left: 25px;
    padding: 10px; /* Add some padding inside */
    font-size: 16px; /* Font size for the text */
    border: 2px solid #ccc; /* Light border */
    border-radius: 25px; /* Rounded corners */
    background-color: #f9f9f9; /* Light background color */
    transition: all 0.3s ease; /* Smooth transition for focus and hover effects */
  }
  
  /* Focus effect */
  #search:focus {
    outline: none; /* Remove the default blue outline */
    border-color: #ccc; /* Change border color on focus */
    background-color: #fff; /* White background when focused */
  }
  
  /* Placeholder styling */
  #search::placeholder {
    color: #888; /* Lighter color for the placeholder */
    font-style: italic; /* Italic style for the placeholder text */
  }

/*Styling Images*/
/* Main container styling */
main {
    display: flex; /* Use flexbox for horizontal layout */
    justify-content: space-between; /* Distribute space between the images */
    gap: 20px; /* Space between images */
    padding: 20px; /* Add some padding around */
  }
  
  /* Individual image container styling */
  .img_1, .img_2, .img_3 {
    flex: 1; /* Allow images to grow and fill available space equally */
    display: flex;
    flex-direction: column; /* Stack image and caption vertically */
    align-items: center; /* Center the content */
    text-align: center; /* Center the text */
    border-radius: 10px; /* Optional rounded corners */
    background-color: #f5f5f5; /* Light background color */
    padding: 15px; /* Padding around content */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow effect */
  }
  
  /* Image styling (Resetting image sizes) */
  .img_1 img, .img_2 img, .img_3 img {
    width: 100%; /* Make images responsive */
    max-width: 200px; /* Set maximum width for all images */
    height: 300px; /* Set fixed height for all images */
    object-fit: cover; /* Ensures the image covers the container without stretching */
    border-radius: 10px; /* Optional rounded corners */
    margin-bottom: 15px; /* Space between image and text */
  }
  
  /* Caption text styling */
  .img_1 p, .img_2 p, .img_3 p {
    font-size: 10px; /* Font size for the caption */
    color: #000000; /* Dark color for readability */
    font-weight:400; /* Bold text */
    margin: 0; /* Remove default margin */
    text-transform: uppercase; /* Make the caption text uppercase */
    letter-spacing: 1px; /* Slight spacing between letters for better readability */
  }
  
  /* Hover effect for the image container */
  .img_1:hover, .img_2:hover, .img_3:hover {
    cursor: pointer;
    transform: translateY(-5px); /* Subtle hover effect */
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15); /* Enhanced shadow on hover */
    transition: all 0.3s ease; /* Smooth transition */
  }
  
  /* Heading 2 styling (for main title or any section title) */
  h2 {
    font-size: 32px; /* Font size for main title */
    color: #333333; /* Dark color for the title */
    font-weight: bold; /* Make the title bold */
    text-align: center; /* Center the title */
    margin-bottom: 20px; /* Space below the title */
    letter-spacing: 1px; /* Slight spacing between letters */
  }
  
  /* Paragraph text styling (for descriptions) */
  p {
    font-size: 16px; /* Font size for paragraph */
    color: #555555; /* Lighter color for the paragraph */
    line-height: 1.6; /* Increase line height for readability */
    text-align: center; /* Center align the paragraph */
    margin-top: 0; /* Remove default top margin */
    margin-bottom: 20px; /* Add space below the paragraph */
  }
  